<template>
  <div>
    <headerComponent
      :title="title"
      :subTitle="subTitle"
      :isLoading="isLoading"
      :pageURL="pageURL"
    ></headerComponent>

    <div class="mdui-container" style="max-width: 600px">
      <div v-if="rData != []">
        <div v-for="data in rData">
          <br /><br />
          <div class="mdui-card mdui-hoverable" style="border-radius: 16px">
            <!-- 卡片头部，包含头像、标题、副标题 -->
            <div class="mdui-card-header">
              <img class="mdui-card-header-avatar" :src="data.avatar" />
              <div class="mdui-card-header-title">{{ data.confessor }}</div>
              <div class="mdui-card-header-subtitle">{{ data.time }}</div>
            </div>

            <!-- 卡片的媒体内容，可以包含图片、视频等媒体内容，以及标题、副标题 -->
            <div class="mdui-card-media">
              <div v-if="data.image != ''">
                <img
                  style="max-height: 1000px"
                  :onerror="randomImage(data.image)"
                  :src="data.image"
                />
              </div>
              <div v-else>
                <div class="mdui-divider"></div>
              </div>
            </div>

            <!-- 卡片的标题和副标题 -->
            <div class="mdui-card-primary">
              <div class="mdui-card-primary-title">To {{ data.to }}</div>
              <div class="mdui-card-primary-subtitle">
                {{ data.introduction }}
              </div>
            </div>

            <!-- 卡片的内容 -->
            <div class="mdui-card-content">
              {{ data.content }}
            </div>
            <!-- 
            <div class="mdui-card-actions">
              <button class="mdui-btn mdui-btn-icon mdui-float-right">
                <i class="mdui-icon material-icons">share</i>
              </button>
              <button class="mdui-btn mdui-btn-icon mdui-float-right">
                <i class="mdui-icon material-icons">more</i>
              </button>
            </div>-->
          </div>
        </div>
      </div>
      <br /><br />
    </div>
  </div>
</template>
<script>
import headerComponent from "../components/header";
import { request } from "../utils/request.js";
export default {
  data() {
    return {
      rData: [],
      title: "未知表白墙",
      subTitle: "主页",
      isLoading: true,
      pageURL: "/",
      imgList: [],
    };
  },
  components: {
    headerComponent: headerComponent,
  },
  methods: {
    getRandom(min, max) {
      return Math.floor(Math.random() * (max - min) + min);
    },
    randomImage() {
      //return "this.src='https://static.llilii.cn/images/kagamine/75578818_p0.jpg'";
      return (
        "this.src='https://static.llilii.cn/images/kagamine/" +
        this.imgList["file_name"][
          this.getRandom(0, this.imgList["file_num"])
        ] +
        "'"
      );
    },
  },
  mounted() {
    request(
      this,
      {
        url: `https://static.llilii.cn/images/kagamine/img_list.json`,
        method: "get",
      },
      function (that, rdata) {
        that.imgList = rdata;
      }
    );
    request(
      this,
      {
        url: `https://api.llilii.cn/loveway/getInfo.php`,
        method: "get",
      },
      function (that, rdata) {
        that.rData = rdata;
      }
    );
  },
};
</script>



